	module.exports = {"data":{"post":{"html":"<p>Some notes on setting up Laravel with React.</p>\n<ol>\n<li>\n<p>Remove the reference to Vue from <code>devDependencies</code>.</p>\n</li>\n<li>\n<p><code>npm install</code>.</p>\n</li>\n<li>\n<p><code>npm install --save react react-dom</code>.</p>\n</li>\n<li>\n<p>Replace the code in <code>resources/assets/js/app.js</code> with the root React boilerplate (importing the root <code>App.js</code> component and rendering it to the DOM).</p>\n</li>\n<li>\n<p>Replace <code>components/Example.vue</code> with <code>App.js</code>.</p>\n</li>\n<li>\n<p>Set up the root div in the home view and route to it if necessary.</p>\n</li>\n<li>\n<p>In <code>webpack.mix.js</code>, replace <code>mix.js</code> with <code>mix.react</code>.</p>\n</li>\n<li>\n<p><code>npm run watch</code>.</p>\n</li>\n</ol>","fields":{"title":"laravel and react","date":"18 July, 2017","url":"http://unlikenesses.com/2017-07-18-laravel-and-react/"}}},"pathContext":{"slug":"/2017-07-18-laravel-and-react/","prev":{"fields":{"title":"react sequencer redux","date":"14 July, 2017","slug":"/2017-07-14-react-sequencer-redux/","_PARENT":"SOURCE"},"_PARENT":"SOURCE"},"next":{"fields":{"title":"rails on windows with vagrant","date":"19 July, 2017","slug":"/2017-07-19-rails-on-windows-with-vagrant/","_PARENT":"SOURCE"},"_PARENT":"SOURCE"},"_PARENT":"SOURCE"}}

